/**
 * basic hover animation timing (of color and opacity).
 * timing and bezier should be refactored after official Porsche animation guidelines are defined
 */
.sg-sidebar {
  position: relative;
  padding: 2.625rem 1.875rem; }
  .sg-sidebar .sg-category {
    margin-top: 1.5rem; }
  .sg-sidebar .sg-title {
    font-weight: bold; }
  .sg-sidebar .sg-nav-item {
    list-style-type: none; }
    .sg-sidebar .sg-nav-item .sg-nav-item {
      display: flex; }
      .sg-sidebar .sg-nav-item .sg-nav-item::before {
        content: "";
        display: flex;
        align-items: center;
        margin-right: 0.75rem; }
  .sg-sidebar .sg-nav-link {
    position: relative;
    display: block;
    padding: 0.375rem 0.375rem 0.375rem 1.125rem;
    text-decoration: none;
    color: #191f22; }
    .sg-sidebar .sg-nav-link--current, .sg-sidebar .sg-nav-link:hover, .sg-sidebar .sg-nav-link:focus, .sg-sidebar .sg-nav-link:active {
      color: #d5001c; }
  .sg-sidebar .sg-nav-link-icon {
    position: absolute;
    left: 0;
    top: 9px; }
