/**
 * basic hover animation timing (of color and opacity).
 * timing and bezier should be refactored after official Porsche animation guidelines are defined
 */
.sg-home-teaser {
  position: relative;
  display: block;
  margin: -1.875rem -1.875rem 0; }
  @media (min-width: 760px) {
    .sg-home-teaser {
      margin: -3.75rem -3.75rem 0; } }
  .sg-home-teaser .sg-img-wrap {
    position: relative;
    overflow: hidden;
    max-height: 240px; }
    @media (min-width: 760px) {
      .sg-home-teaser .sg-img-wrap {
        min-height: 340px; } }
    @media (min-width: 1000px) {
      .sg-home-teaser .sg-img-wrap {
        min-height: 400px; } }
    @media (min-width: 1760px) {
      .sg-home-teaser .sg-img-wrap {
        min-height: 500px; } }
  .sg-home-teaser .sg-img {
    position: relative;
    max-width: 100%; }
    @media (min-width: 760px) {
      .sg-home-teaser .sg-img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%); } }
  .sg-home-teaser .sg-text {
    color: #000;
    padding: 0 1.875rem; }
    @media (min-width: 760px) {
      .sg-home-teaser .sg-text {
        position: absolute;
        left: 0;
        right: 0;
        top: 50%;
        padding: 0 3.75rem;
        transform: translateY(-50%);
        color: #fff; } }
  .sg-home-teaser .sg-headline {
    position: absolute;
    left: 0;
    right: 0;
    top: 3.75rem;
    padding: 0 1.875rem;
    color: #fff; }
    @media (min-width: 760px) {
      .sg-home-teaser .sg-headline {
        position: static;
        padding: 0 3.75rem; } }
  .sg-home-teaser .sg-headline-large {
    display: block;
    line-height: 1;
    font-size: 3rem; }
    @media (min-width: 1300px) {
      .sg-home-teaser .sg-headline-large {
        font-size: 4.5rem; } }
    @media (min-width: 1760px) {
      .sg-home-teaser .sg-headline-large {
        font-size: 6.1875rem; } }
  .sg-home-teaser .sg-subline {
    margin-top: 1.125rem; }

.sg-mail-teaser {
  padding: 1.5rem;
  background-color: #eff0f1; }

.sg-home-content {
  padding-top: 1.875rem; }
