/**
 * basic hover animation timing (of color and opacity).
 * timing and bezier should be refactored after official Porsche animation guidelines are defined
 */
.sg-footer {
  position: fixed;
  position: sticky;
  width: 299px;
  padding: 1.875rem;
  top: 100%;
  bottom: 0;
  color: #959899;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, white 1.5rem, white 100%); }
  .sg-footer a {
    color: #959899;
    text-decoration: underline; }
    .sg-footer a:hover, .sg-footer a:focus {
      color: #d5001c; }
