/**
 * basic hover animation timing (of color and opacity).
 * timing and bezier should be refactored after official Porsche animation guidelines are defined
 */
.sg-app .sg-toggle-sidebar {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 10;
  padding: 0 0.375rem;
  height: 30px;
  background: #fff;
  border: 1px solid #afb1b2;
  cursor: pointer;
  color: #000;
  font-family: "Porsche Next", "Arial Narrow", Arial, sans-serif;
  font-weight: 600;
  font-size: 0.875rem;
  line-height: 150%; }
  .sg-app .sg-toggle-sidebar:focus {
    outline: 0; }

.sg-app .sg-area-sidebar {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  background-color: #fff;
  width: 300px;
  height: 100%;
  z-index: 1;
  overflow-x: hidden;
  border-right: 1px solid #c8cacb;
  transition: transform 400ms ease-out, opacity 400ms ease-out;
  will-change: transform, opacity; }
  .sg-app .sg-area-sidebar--close {
    opacity: 0;
    transform: translateX(-100%); }

.sg-app .sg-area-content {
  display: block;
  padding: 1.875rem;
  max-width: 1620px;
  margin-left: 300px;
  transition: margin-left 400ms ease-out; }
  @media (min-width: 760px) {
    .sg-app .sg-area-content {
      padding: 3.75rem; } }
  .sg-app .sg-area-content--full {
    margin-left: 0;
    max-width: 1920px; }
