/**
 * basic hover animation timing (of color and opacity).
 * timing and bezier should be refactored after official Porsche animation guidelines are defined
 */
.sg-props {
  overflow-x: auto; }
  .sg-props .sg-component-description {
    white-space: pre-wrap; }
  .sg-props .sg-table {
    min-width: 900px; }
  .sg-props .sg-cell-name {
    width: 15%;
    min-width: 100px; }
  .sg-props .sg-cell-default {
    width: 15%;
    min-width: 100px; }
  .sg-props .sg-cell-type {
    width: 25%;
    min-width: 200px; }
  .sg-props .sg-cell-desc {
    white-space: pre-wrap; }
  .sg-props + .sg-props {
    margin-top: 3.75rem; }
